body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color:lightgray;
}

.container{
	display:-ms-flexbox;
	display:flex;
	-ms-flex-wrap:wrap;
	    flex-wrap:wrap;
	text-align:center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column; /* make main-axis vertical */
  margin: 50px auto;
  width: 50%; 
}
.box{
  width:100%;
  margin: 0px auto;
  
}
@media only screen and ( max-width:768px ){
	
	.box{
		width:100%;
	}
}


